import * as dayjs from "dayjs";
import { useContext, useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useSelector } from "react-redux";
import { SidebarContext } from "context/SidebarContext";
import CouponServices from "services/CouponServices";
import { notifyError, notifySuccess } from "utils/toast";
import { addCoupons } from "services/Api";
import moment from "moment";

const useCouponSubmit = (
  id,
  getAllCoupansList,
  isEdit,
  setIsEdit,
  pagenumber
) => {
  const { isDrawerOpen, closeDrawer, setIsUpdate, lang, toggleDrawer } =
    useContext(SidebarContext);
  const [imageUrl, setImageUrl] = useState("");
  const [language, setLanguage] = useState(lang);
  const [resData, setResData] = useState({});
  const [published, setPublished] = useState(false);
  const [discountType, setDiscountType] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const settings = useSelector((state) => state.setting);
  const { settingItem } = settings;
  const [dates, setDates] = useState([]);
  const [Dvalue, setDvalue] = useState({});
  const [openModal, setOpenModal] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [Evalue, setEvalue] = useState({});
  const globalSetting = settingItem.find(
    (value) => value.name === "globalSetting"
  );
  const currency = globalSetting?.default_currency || "";

  const {
    register,
    handleSubmit,
    setValue,
    clearErrors,
    reset,
    watch,
    formState: { errors },
  } = useForm();
  const onCloseModal = () => {
    toggleDrawer();
    setOpenModal(false);
    reset();
    setDates([]);
    setDvalue({});
    setEvalue({});
  };
  const onSubmit = async (data) => {
    try {
      setIsSubmitting(true);
      console.log(Dvalue, "Dvalue");
      if (!Dvalue) {
        setErrorMsg("Please select a valid start date.");
      } else {
        setErrorMsg("");
        console.log("coupon data", Dvalue, Evalue);
        let payload = {
          Coupon_properties: data.PropertyId?.map((item) => {
            return { property_id: item };
          }),
          category: data.Category,

          // package_id: data.PackagesID,
          name: data.CouponName,
          code: data.couponCode,
          type: discountType ? "percentage" : "fixed_amount",
          discount: data.discountPercentage,
          start_date: new Date(Dvalue),
          end_date: new Date(Evalue),
          total: data?.Total,
          per_user: data?.PerUser,
          coupon_block_dates: dates,
        };
        if (data?.Category === "dayout" || data?.Category === "stay") {
          payload["Coupon_packages"] = data.PackagesID?.map((item) => {
            return { package_id: item };
          });
        }
        if (data?.Category === "event") {
          payload["Coupon_events"] = data.PackagesID?.map((item) => {
            return { event_id: item };
          });
        }
        console.log(payload, "payload");
        if (isEdit) {
          payload["id"] = data?.id;
        }
        console.log(data, "data");
        const res = await addCoupons(payload, isEdit);
        if (res.data.status == true) {
          console.log(res, "dsfs");
          setIsEdit(false);
          handleClearVariant();
          setIsSubmitting(false);
          getAllCoupansList(pagenumber);
          closeDrawer();
          toggleDrawer();
          notifySuccess(res.data.message);
        }
        // const couponData = {
        //   title: {
        //     [language]: data.title,
        //   },
        //   couponCode: data.couponCode,
        //   endTime: data.endTime,
        //   minimumAmount: data.minimumAmount,
        //   logo: imageUrl,
        //   lang: language,
        //   status: published ? "show" : "hide",
        //   discountType: {
        //     type: discountType ? "percentage" : "fixed",
        //     value: data.discountPercentage,
        //   },
        //   productType: data.productType,
        // };
      }
      setIsSubmitting(false);
    } catch (err) {
      notifyError(err ? err?.response?.data?.message : err.message);
      setIsSubmitting(false);
      closeDrawer();
    }
  };

  const handleClearVariant = () => {
    setImageUrl([]);
    setDates([]);
    setDvalue({});
    setEvalue({});
    setValue("PropertyId", "");
    setValue("Category", "");
    setValue("PackagesID", "");
    setValue("CouponName", "");
    setValue("couponCode", "");
    setValue("discountType", "");
    setValue("discountPercentage", "");
    // setValue("StartDate", "");
    // setValue("endTime", "");
    setValue("total", "");
    setValue("Total", "");
    setValue("PerUser", "");
  };

  return {
    register,
    handleSubmit,
    onSubmit,
    errors,
    setImageUrl,
    imageUrl,
    published,
    setPublished,
    currency,
    discountType,
    onCloseModal,
    errorMsg,
    setErrorMsg,
    isSubmitting,
    setDiscountType,
    setDates,
    Dvalue,
    Evalue,
    setEvalue,
    setDvalue,
    dates,
    setValue,
    watch,
  };
};

export default useCouponSubmit;
