import { TableBody, TableCell, TableRow } from "@windmill/react-ui";
import DeleteModal from "components/coupons/DeleteCoupons";
import ShowHideButton from "components/table/ShowHideButton";
import Tooltip from "components/tooltip/Tooltip";
import useToggleDrawer from "hooks/useToggleDrawer";
import { t } from "i18next";
import { useState, useContext } from "react";
import { FiEdit, FiTrash2 } from "react-icons/fi";
import { useHistory } from "react-router-dom";
import moment from "moment";
import { SidebarContext } from "context/SidebarContext";
import { getCoupanById } from "services/Api";

const ProductTable = ({
  products,
  isCheck,
  pagenumber,
  setIsCheck,
  getAllCoupansList,
  setCoupondata,
  setIsEdit,
  wholeCount,
}) => {
  const { toggleModal, toggleDrawer } = useContext(SidebarContext);
  const { title, handleUpdate } = useToggleDrawer();
  const [serviceId, setserviceId] = useState("");
  const history = useHistory();
  const format = "MM/DD/YYYY";
  const handleClick = (e) => {
    const { id, checked } = e.target;
    console.log("id", id, checked);

    setIsCheck([...isCheck, id]);
    if (!checked) {
      setIsCheck(isCheck.filter((item) => item !== id));
    }
  };

  const handleUpdate1 = async (properties) => {
    console.log(properties, "clicked");
    const singleview = await getCoupanById(properties.id);
    if (singleview && singleview.status === 200) {
      console.log(singleview?.data?.data?.Coupon, "singleview");
      setCoupondata(singleview?.data?.data?.Coupon);
      toggleDrawer();
      // setCoupondata(properties);
      setIsEdit(true);
    }
  };

  const handleModalOpen = (data) => {
    toggleModal();
    console.log("clicked---", data);
    setserviceId(data);
  };

  return (
    <>
      {isCheck?.length < 1 && (
        <DeleteModal
          getAllCoupansList={getAllCoupansList}
          id={serviceId}
          title={title}
        />
      )}
      <TableBody>
        {products?.map((properties, i) => (
          <TableRow key={i + 1}>
            <TableCell>
              <span className="text-sm">
                {(wholeCount?.currentPage - 1) * wholeCount?.pageSize + i + 1}
              </span>
            </TableCell>
            <TableCell className="">
              {properties?.Coupon_properties?.map((item, index) => (
                <>
                  {" "}
                  <span className="text-sm ">
                    {item?.Property?.name}{" "}
                    {index !== properties?.Coupon_properties?.length - 1
                      ? ","
                      : ""}
                  </span>
                  <br />
                </>
              ))}
            </TableCell>
            {/* <TableCell className="">
              <span className="text-sm ">{properties?.Package?.name || properties?.Event?.name}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm ">{properties?.category}</span>
            </TableCell> */}
            <TableCell className="">
              <span className="text-sm ">{properties?.name}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">{properties?.code}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">{properties?.type}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">{properties?.discount}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">
                {" "}
                {moment(properties?.start_date).format("MMMM Do YYYY")}
              </span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">
                {" "}
                {moment(properties?.end_date).format("MMMM Do YYYY")} {}
              </span>
            </TableCell>
            <TableCell className="">
              {properties?.Coupon_block_dates?.map((bro) => (
                <span className="text-sm">
                  {moment(bro?.date).format("MMMM Do YYYY")}
                  <br />
                </span>
              ))}
            </TableCell>
            <TableCell>
              <button
                onClick={() => handleUpdate1(properties)}
                className="p-2 cursor-pointer text-gray-400 hover:text-green-600 focus:outline-none"
              >
                <Tooltip
                  id="edit"
                  Icon={FiEdit}
                  title={t("Edit1")}
                  bgColor="#10B981"
                />
              </button>
              <button
                onClick={() =>
                  handleModalOpen(properties?.id, "title", "product")
                }
                className="p-2 cursor-pointer text-gray-400 hover:text-red-600 focus:outline-none"
              >
                <Tooltip
                  id="delete"
                  Icon={FiTrash2}
                  title={t("Delete")}
                  bgColor="#EF4444"
                />
              </button>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </>
  );
};

export default ProductTable;
