import React, { useContext, useEffect, useState } from "react";
import {
  Table,
  TableHeader,
  TableCell,
  TableFooter,
  TableContainer,
  Button,
  Card,
  CardBody,
  Pagination,
  Input,
} from "@windmill/react-ui";
import { useTranslation } from "react-i18next";
import { FiPlus } from "react-icons/fi";
import useAsync from "hooks/useAsync";
import NotFound from "components/table/NotFound";
import ProductServices from "services/ProductServices";
import PageTitle from "components/Typography/PageTitle";
import { SidebarContext } from "context/SidebarContext";
import ProductTable from "components/coupons/couponsTables";
import MainDrawer from "components/drawer/MainDrawer";
import TableLoading from "components/preloader/TableLoading";
import SettingServices from "services/SettingServices";
import CouponDrawer from "components/coupons/AddCoupans";
import { DayOutCoupansList } from "services/Api";

const Products = () => {
  const { t } = useTranslation();
  const {
    toggleDrawer,
    lang,
    currentPage,
    searchText,
    category,
    handleSubmitForAll,
    sortedField,
    limitData,
  } = useContext(SidebarContext);

  const { data } = useAsync(() =>
    ProductServices.getAllProducts({
      page: currentPage,
      limit: limitData,
      category: category,
      title: searchText,
      price: sortedField,
    })
  );

  const { data: globalSetting } = useAsync(SettingServices.getGlobalSetting);
  const [pagenumber, setpagenumber] = useState(1);
  const [loading, setLoading] = useState(true);
  const [coupans, setcoupans] = useState([]);
  const [isCheck, setIsCheck] = useState([]);
  const [isEdit, setIsEdit] = useState(false);
  const [couponData, setCoupondata] = useState({});
  const [wholeCount, setwholeCount] = useState({});
  useEffect(() => {
    getAllCoupansList(pagenumber);
  }, []);

  const getAllCoupansList = async (pagenumber, search = "") => {
    const coupans = await DayOutCoupansList(pagenumber, search);
    console.log(coupans.data.data, "coupans");
    setLoading(false);
    setcoupans(coupans?.data?.data);
    setwholeCount(coupans?.data?.data?.pagination);
  };
  const handleChangePage = (e) => {
    console.log("clicked", e);
    setpagenumber(e);
    getAllCoupansList(e);
  };

  const handleSearch = (search) => {
    getAllCoupansList(pagenumber, search);
  };
  return (
    <>
      <PageTitle>{t("Coupons")}</PageTitle>
      <MainDrawer>
        <CouponDrawer
          getAllCoupansList={getAllCoupansList}
          isEdit={isEdit}
          setIsEdit={setIsEdit}
          pagenumber={pagenumber}
          couponData={couponData}
          setCoupondata={setCoupondata}
        />
      </MainDrawer>
      <Card className="min-w-0 shadow-xs overflow-hidden bg-white dark:bg-gray-800 mb-5">
        <CardBody className="">
          <form
            onSubmit={handleSubmitForAll}
            className="py-3 md:pb-0 grid gap-4 lg:gap-6 xl:gap-6  xl:flex"
          >
            {" "}
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <Input
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="search"
                onChange={(e) => handleSearch(e.target.value)}
                name="search"
                placeholder={t("StaffSearchBy")}
              />
              <button
                type="submit"
                className="absolute right-0 top-0 mt-5 mr-1"
              ></button>
            </div>
            <div className="lg:flex  md:flex xl:justify-end xl:w-1/2  md:w-full md:justify-start flex-grow-0">
              <div className="w-full md:w-48 lg:w-48 xl:w-48">
                <Button
                  onClick={toggleDrawer}
                  className="w-full rounded-md h-12"
                >
                  <span className="mr-2">
                    <FiPlus />
                  </span>
                  {t("Add Coupons")}
                </Button>
              </div>
            </div>
          </form>
        </CardBody>
      </Card>

      {loading ? (
        <TableLoading row={12} col={7} width={160} height={20} />
      ) : coupans?.length !== 0 ? (
        <TableContainer className="mb-8 rounded-b-lg">
          <Table>
            <TableHeader>
              <tr>
                <TableCell className="text-left">{"S.No"}</TableCell>
                <TableCell>{t("Property Name")}</TableCell>
                {/* <TableCell>{t("Package / Events Name")}</TableCell>
                <TableCell>{t("Category Name")}</TableCell> */}
                <TableCell>{t("Coupon Name")}</TableCell>
                <TableCell>{t("Coupon Code")}</TableCell>
                <TableCell>{t("Discount Type")}</TableCell>
                <TableCell>{t("Discount")}</TableCell>
                <TableCell>{t("Start Date")}</TableCell>
                <TableCell>{t("End Date")}</TableCell>
                <TableCell>{t("Blocked Date")}</TableCell>
                {/* <TableCell className="text-center">{t("Status")}</TableCell> */}
                <TableCell className="text-right">{t("Action")}</TableCell>
              </tr>
            </TableHeader>
            <ProductTable
              setCoupondata={setCoupondata}
              lang={lang}
              isCheck={isCheck}
              products={coupans?.Coupons}
              setIsCheck={setIsCheck}
              pagenumber={pagenumber}
              toggleDrawer={toggleDrawer}
              setIsEdit={setIsEdit}
              getAllCoupansList={getAllCoupansList}
              wholeCount={wholeCount}
            />
          </Table>
          <TableFooter>
            <Pagination
              totalResults={coupans?.pagination?.total}
              resultsPerPage={coupans?.pagination?.pageSize}
              onChange={handleChangePage}
              label="Product Page Navigation"
            />
          </TableFooter>
        </TableContainer>
      ) : (
        <NotFound title="Coupons" />
      )}
    </>
  );
};

export default Products;
