import { Input, Select } from "@windmill/react-ui";
import DrawerButton from "components/form/DrawerButton";
import Error from "components/form/Error";
import InputArea from "components/form/InputArea";
import InputValue from "components/form/InputValue";
import LabelArea from "components/form/LabelArea";
import SwitchToggleFour from "components/form/SwitchToggleFour";
import Title from "components/form/Title";
import useCouponSubmit from "hooks/useCouponSubmit";
import { t } from "i18next";
import { useEffect, useState } from "react";
import { Scrollbars } from "react-custom-scrollbars-2";
import { getAllCommonProperyList, getAllPackageForCoupon } from "services/Api";
import DatePicker, { DateObject } from "react-multi-date-picker";
import DatePanel from "react-multi-date-picker/plugins/date_panel";
import moment from "moment";

const CouponDrawer = ({
  id,
  getAllCoupansList,
  setCoupondata,
  couponData,
  pagenumber,
  setIsEdit,
  isEdit,
}) => {
  const {
    register,
    handleSubmit,
    onSubmit,
    errors,
    published,
    setPublished,
    Evalue,
    setEvalue,
    setImageUrl,
    imageUrl,
    currency,
    discountType,
    Dvalue,
    onCloseModal,
    setDvalue,
    setDates,
    errorMsg,
    setErrorMsg,
    dates,
    watch,
    setDiscountType,
    isSubmitting,
    handleSelectLanguage,
    setValue,
  } = useCouponSubmit(id, getAllCoupansList, isEdit, setIsEdit, pagenumber);
  const format = "MM/DD/YYYY";
  const [propertyList, setpropertyList] = useState([]);
  const [packagesList, setpackagesList] = useState([]);
  const [proId, setproId] = useState("");
  const [Cate, setCate] = useState("dayout");
  const [loading, setLoading] = useState(false);
  const categoryWatch = watch("Category");
  const propertyWatch = watch("PropertyId");
  const [isupdated, setIsUpdate] = useState(false);
  useEffect(() => {
    getPropertyList();
    // getAllPackagesList(proId,Cate);
    console.log(dates, "dates");
  }, []);
  const getPropertyList = async () => {
    const PropertyList = await getAllCommonProperyList();
    console.log(
      PropertyList?.data?.data?.propertyList,
      "getAllcommonProperyList"
    );
    setpropertyList(PropertyList?.data?.data?.propertyList);
    setLoading(false);
  };
  const getAllPackagesList = async (pId, cId) => {
    const body = {
      property_id: pId,
      category: cId,
    };
    const packagesList = await getAllPackageForCoupon(body);
    console.log(packagesList?.data?.data?.packages, "packagesList");
    setpackagesList(packagesList?.data?.data?.packages);
    setIsUpdate(true);
  };
  const setDates1 = (e) => {
    console.log(e, "eee");
    setDates(e?.map((date) => date?.format("MM/DD/YYYY")));
  };
  const containsSpecialCharsOrNumbers = (value) => {
    return /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?0-9]/.test(value);
  };
  useEffect(() => {
    if (isEdit) {
      console.log(couponData, "couponData");
      setValue(
        "PropertyId",
        couponData?.Coupon_properties?.map((item) =>
          item?.property_id?.toString()
        )
      );

      setValue("Category", couponData?.category);
      // setValue("PackagesID", couponData?.package_id);
      setValue("CouponName", couponData?.name);
      setValue("couponCode", couponData?.code);
      setDvalue(couponData?.start_date);
      setEvalue(couponData?.end_date);
      setDiscountType(couponData?.type === "percentage" ? true : false);
      setValue("discountPercentage", couponData?.discount);
      setValue("Total", couponData?.total);
      setValue("PerUser", couponData?.per_user);
      setValue("id", couponData?.id);
      setDates(
        couponData?.Coupon_block_dates?.map((item) =>
          moment(item?.date).format("MM/DD/YYYY")
        )
      );
      if (
        couponData?.category === "dayout" ||
        couponData?.category === "stay"
      ) {
        setValue(
          "PackagesID",
          couponData?.Coupon_packages?.map((item) =>
            item?.package_id?.toString()
          )
        );
      }
      if (couponData?.category === "event") {
        setValue(
          "PackagesID",
          couponData?.Coupon_events?.map((item) => item?.event_id?.toString())
        );
      }
    }
  }, [couponData, isEdit, isupdated]);

  const handleDateChange = (newDate) => {
    setErrorMsg("");
    setDvalue(newDate);
  };
  const handleSelectChange = (e) => {
    console.log(e.target.value, "e.target.value");
    setproId(e.target.value);
    // getAllPackagesList(e.target.value, Cate);
  };
  const handleSelectcategoryChange = (e) => {
    console.log(e.target.value, "data");
    setCate(e.target.value);
    getAllPackagesList(proId, e.target.value);
  };

  useEffect(() => {
    console.log(propertyWatch, categoryWatch, "categoryWatch");
    if (
      propertyWatch?.length > 0 &&
      categoryWatch &&
      categoryWatch !== "property"
    ) {
      getAllPackagesList(propertyWatch, categoryWatch);
    }
  }, [propertyWatch, categoryWatch]);

  return (
    <>
      <div className="w-full relative  p-6 border-b border-gray-100 bg-gray-50 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300 ">
        {isEdit ? (
          <Title
            register={register}
            handleSelectLanguage={handleSelectLanguage}
            title={t("UpdateCoupon")}
            description={t("UpdateCouponDescription")}
          />
        ) : (
          <Title
            register={register}
            handleSelectLanguage={handleSelectLanguage}
            title={t("AddCoupon")}
            description={t("AddCouponDescription")}
          />
        )}
      </div>

      {/* onChange={(e) => {
                      setDvalue(e.target.value);
                      handleDateChange()
                    }} */}
      <Scrollbars className="w-full md:w-7/12 lg:w-8/12 xl:w-8/12 relative dark:bg-gray-700 dark:text-gray-200">
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="px-6 pt-8 flex-grow scrollbar-hide w-full max-h-full pb-40">
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Property")} />
              <div className="col-span-8 sm:col-span-4">
                <Select
                  {...register(`PropertyId`, {
                    required: "Select Property is required!",
                  })}
                  multiple
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                  // onChange={(e) => handleSelectChange(e)}
                >
                  {/* <option value="" defaultValue hidden>
                    {t("Select property")}
                  </option> */}
                  {propertyList?.map((pro) => (
                    <option value={pro.id}>{pro.name}</option>
                  ))}
                </Select>
                <Error errorName={errors.PropertyId} pos />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Category")} />
              <div className="col-span-8 sm:col-span-4">
                <Select
                  {...register(`Category`, {
                    required: "Select Category is required!",
                  })}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                  // onChange={(e) => handleSelectcategoryChange(e)}
                >
                  <option value="" defaultValue hidden>
                    {t("Select Category")}
                  </option>
                  <option value="property">Property</option>
                  <option value="dayout">Dayout Packages</option>
                  <option value="event">Event</option>
                  <option value="stay">Stay</option>
                </Select>
                <Error errorName={errors.Category} pos />
              </div>
            </div>

            {categoryWatch !== "property" && (
              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Packages")} />
                <div className="col-span-8 sm:col-span-4">
                  <Select
                    {...register(`PackagesID`, {
                      required: "Select Packages is required!",
                    })}
                    multiple
                    className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                  >
                    <option value="" defaultValue hidden>
                      {t("Select Packages")}
                    </option>
                    {packagesList?.map((pro) => (
                      <option value={pro.id}>{pro.name}</option>
                    ))}
                  </Select>
                  <Error errorName={errors.PackagesID} pos />
                </div>
              </div>
            )}

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t(" Coupon Name")} />
              <div className="col-span-8 sm:col-span-4">
                <Input
                  {...register("CouponName", {
                    required: "Coupon name required!",
                    validate: (value) =>
                      !containsSpecialCharsOrNumbers(value) ||
                      "Name cannot contain special characters or numbers",
                  })}
                  label="Coupon Name"
                  name="CouponName"
                  type="text"
                  placeholder={t("Coupon Name")}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                />
                <Error errorName={errors.CouponName} />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("CampaignCode")} />
              <div className="col-span-8 sm:col-span-4">
                <InputArea
                  register={register}
                  label="Campaign Code"
                  name="couponCode"
                  type="text"
                  placeholder={t("CampaignCode")}
                />
                <Error errorName={errors.couponCode} />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Start Date")} />
              <div className="col-span-8 sm:col-span-4">
                {/* <Input
                  {...register(`StartDate`, {
                    required: "Coupon Start Date",
                  })}
                  label="Coupon Start Date"
                  name="StartDate"
                  type="Date"
                  placeholder={t("Start Date")}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 dark:bg-white border-transparent focus:bg-white"
                /> */}
                <div className="cy">
                  <DatePicker
                    minDate={new Date()}
                    placeholder="Please Enter Start Date"
                    calendarPosition="bottom-center"
                    name="StartDate"
                    required
                    value={Dvalue}
                    onChange={(e) => setDvalue(e)}
                  />
                </div>
                <Error errorName={errors.StartsDate} />
              </div>
            </div>
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("End Date")} />
              <div className="col-span-8 sm:col-span-4">
                <div className="cy">
                  <DatePicker
                    minDate={Dvalue}
                    placeholder="Please Enter End Date"
                    calendarPosition="bottom-center"
                    required
                    name="StartDate"
                    value={Evalue}
                    onChange={(e) => setEvalue(e)}
                  />
                </div>

                <Error errorName={errors.endTime} />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("DiscountType")} />
              <div className="col-span-8 sm:col-span-4">
                <SwitchToggleFour
                  handleProcess={setDiscountType}
                  processOption={discountType}
                />
                <Error errorName={errors.discountType} />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={"Discount"} />
              <div className="col-span-8 sm:col-span-4">
                <InputValue
                  product
                  register={register}
                  maxValue={discountType ? 99 : 1000}
                  minValue={1}
                  label="discount Percentage"
                  name="discountPercentage"
                  type="number"
                  placeholder={discountType ? "Percentage" : "Fixed Amount"}
                  currency={discountType ? "%" : currency}
                />

                <Error errorName={errors.discountPercentage} />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Total")} />
              <div className="col-span-8 sm:col-span-4">
                <Input
                  {...register(`Total`, {
                    required: "Total Amount Required!",
                    pattern: {
                      value: /^[0-9]*$/,
                      message: "Please enter Total Amount valid number.",
                    },
                  })}
                  label="Total"
                  name="Total"
                  type="Text"
                  placeholder={t("Total")}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 dark:bg-white border-transparent focus:bg-white"
                />

                <Error errorName={errors.Total} />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Per User")} />
              <div className="col-span-8 sm:col-span-4">
                <Input
                  {...register(`PerUser`, {
                    required: "Per User Require",
                    pattern: {
                      value: /^[0-9]*$/,
                      message: "Please enter Total Amount valid number.",
                    },
                  })}
                  label="Per User Required"
                  name="PerUser"
                  type="Text"
                  placeholder={t("PerUser")}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 dark:bg-white border-transparent focus:bg-white"
                />

                <Error errorName={errors.PerUser} />
              </div>
            </div>

            {/* <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Block Dates")} />
              <div className="col-span-8 sm:col-span-4">
                <Input
                  {...register(`BlockDates`, {
                    required: "BlockDates",
                  })}
                  label="BlockDates"
                  name="BlockDates"
                  type="Date"
                  placeholder={t("BlockDates")}
                  className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 dark:bg-white border-transparent focus:bg-white"
                />

                <Error errorName={errors.BlockDatess} />
              </div>
            </div> */}
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label={t("Block Dates")} />
              <div className="col-span-8 sm:col-span-4 ">
                <div className="cy">
                  <DatePicker
                    value={dates}
                    //   onChange={setDates}
                    onChange={setDates1}
                    multiple
                    sort
                    minDate={Dvalue}
                    maxDate={Evalue}
                    format={format}
                    placeholder="Please Enter Blocks Date"
                    calendarPosition="bottom-center"
                    plugins={[<DatePanel />]}
                  />
                </div>
              </div>
              <Error errorName={errors.BlockDatess} />
            </div>
          </div>
          <DrawerButton
            onCloseModal={onCloseModal}
            isEdit={isEdit}
            id={id}
            setImageUrl={setImageUrl}
            imageUrl={imageUrl}
            title="Coupon"
            isSubmitting={isSubmitting}
          />
        </form>
      </Scrollbars>
    </>
  );
};
export default CouponDrawer;
